<template>
    <div class="popup-info">
        <div>
        </div>
        
        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">      ,<br>  </div>
            
            <p class="mt24">
                     ,   <br>
                      .
            </p>
        </div>
        
        <div class="mt32">
            <span class="btn btn--white-red" @click="close">!</span>
        </div>
        
        <div class="mt32">
            P.S.      <a @click="openCouponClaim" href="javascript:void(0)">  </a>  .
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'
    
    export default {
        name: "OpinionError",
        methods: {
            close() {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
            },
            openCouponClaim: function () {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
                this.$store.commit('popups/open', {name: this.popups.couponClaim.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

